/**
 * System imports
 ============================================================================ */
import React from "react";
import { Accordion, Card } from "react-bootstrap";
import AccordionCard from "../../../accordionCard";

/**
   * User imports
   ============================================================================ */
// import SubmitButton from "../../../submitButton";
// import Card from "../../../card";

/**
 * Default export
 */
export default (args) => {
  /**
      * Default Properties
      ============================================================================ */
  let defaults = { namespace: "LeadsLogIn" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
      * Barrels
      ============================================================================ */
  const { helpPath3 } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { setHelpPath } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  // const onHelpPath3Click = (link) => {
  //   // console.log(link);
  //   setHelpPath(3, link);
  // };

  /**
      * Return
      ============================================================================ */
  return (
    <>
      <p className="help-title">My Account</p>

      <Accordion defaultActiveKey={helpPath3}>
        <AccordionCard
          title="Manage settings"
          eventkey="manage-settings"
          active={helpPath3}
          onClick={() => setHelpPath(3, "manage-settings")}
        >
          <ul className="topic-ul">
            <li>
              <a href="/article/how-do-i-view-or-edit-my-account-information">
                How do I view or edit my account information?
              </a>
            </li>
            <li>
              <a href="/article/what-are-the-fees-for-lc-accounts">
                What kind of documentation can I provide to remove my account
                limitation?
              </a>
            </li>
            <li>
              <a href="/article/questions-about-daily-allowance-payments">
                Questions about daily allowance payments
              </a>
            </li>
          </ul>
        </AccordionCard>
        <AccordionCard
          title="Account Status & Verifications"
          eventkey="account-status-and-verification"
          active={helpPath3}
          onClick={() => setHelpPath(3, "account-status-and-verification")}
        >
          <ul className="topic-ul">
            <li>
              <a href="/article/customer-identification-program">
                Customer Identification Program (CIP)
              </a>
            </li>
            <li>
              <a href="/article/cip-acceptable-documents-guidelines">
                CIP Acceptable Documents guidelines
              </a>
            </li>
            <li>
              <a href="/article/am-i-being-asked-to-complete-a-security-check-so-i-can-verify-my-account">
                Am I being asked to complete a security check so I can verify my
                account?
              </a>
            </li>
          </ul>
        </AccordionCard>
      </Accordion>
    </>
  );
};
